*{
    box-sizing: border-box;
}

html, body {
    padding: 0;
    margin: 0;
}

.BurgerMenu__container {
    cursor: pointer;
    margin-left: 20px;
    display: flex;
    flex-direction: column;

    i {
        background-color: gray;
        width: 32px;
        height: 4px;
        margin: 4px;
        border-radius: 2px;
        transition: all ease-in-out 0.5s;
    }
    .open:nth-child(1){
        transform: rotate(45deg) translateY(16px);
    }
    .open:nth-child(2){
        opacity: 0;
    }
    .open:nth-child(3){
        transform: rotate(-45deg) translateY(-16px);
    }



    .close:nth-child(1) {
        transform: rotate(0) translateY(0);
    }
    .close:nth-child(2) {
        opacity: 1;
    }
    .close:nth-child(3) {
        transform: rotate(0) translateY(0);
    }
}